.des-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 4em;
}
.name-text{
    margin-top: 3em;
    font-weight: bold;
    text-align: center;
}
.video-react{
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    margin-top: 4em;
    margin-bottom: 8em;
}
.text-left{
    
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    align-items: flex-start;
}
.text-card{
    padding: 1em 1em;
    margin:0 auto;
}
.img-container{
    margin-top: 2em;
}
.card-curiosity{
    padding: 1em;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.power-titan{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0.6em;
}
.container-video{
    display: flex;
    justify-content: center;
    align-items: center;
}

.player-wrapper{
    width: 70vw;
    height:70vh;
    margin-bottom: 5em;
}
.title-player{
    margin-top: 1.5em;
margin-bottom: 2em;
}

@media only screen and (max-width: 600px) {

    .player-wrapper{
        width: 92vw;
        height:35vh;
        margin-bottom: 5em;

    }
}